.map-container {
  height: 400px; }

.map-address-padding {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 30px; }

.map-address-text {
  font-family: 'Roboto Slab', serif; }

.map-heading-container {
  display: inline-block;
  text-align: center;
  width: 100%;
  padding-top: 30px;
  padding-bottom: 30px; }

.map-heading-container-phonescreen {
  display: flex;
  flex-direction: column;
  align-items: center; }

.map-heading-wrapper {
  display: flex;
  justify-content: center; }

.map-heading {
  padding-top: 30px;
  width: 250px;
  font-family: 'Josefin Slab', serif;
  border: 1.5px solid black;
  padding: 15px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }
