textarea:focus, input:focus, div:focus {
  outline: none;
  background-color: rgba(255, 255, 255, 0.8); }

.contact-form-background {
  width: 100%;
  padding-bottom: 200px;
  background-image: url("/static/bgimg6.jpg");
  background-size: cover; }

.contact-form-align {
  display: flex;
  justify-content: center; }

.contact-form-container {
  margin-top: 100px; }

@media (min-width: 951px) {
  .contact-form-container-width {
    width: 50%; } }

@media (max-width: 950px) {
  .contact-form-container-width {
    width: 90%; } }

.contact-form-heading {
  display: flex;
  justify-content: center;
  padding-top: 30px;
  padding-bottom: 30px;
  color: white;
  font-family: 'Josefin Slab', serif;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }

.align-contact-inputs {
  display: flex;
  justify-content: center;
  padding-left: 0px !important;
  padding-right: 0px !important; }

.contact-label-container {
  color: white;
  margin-left: 25%; }

.contact-input-wrapper {
  margin: 5px;
  height: 40px;
  display: flex;
  align-items: center;
  background: transparent;
  width: 100%; }

.contact-form-input {
  width: 100%;
  height: 100%;
  border: none;
  padding-left: 10px;
  font-family: 'Roboto Slab', serif; }

.contact-form-error-class {
  border: 1px solid rgba(200, 0, 0, 0.8);
  background-color: rgba(255, 240, 244, 0.9); }

.textarea-wrapper {
  width: 100%;
  margin: 5px;
  height: 200px;
  display: flex;
  align-items: flex-start;
  background: transparent; }

.effect-1 {
  border: 0;
  padding: 7px 0;
  border-bottom: 1px solid #ccc; }

.effect-1 ~ .focus-border {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #4caf50;
  transition: 0.4s; }

.effect-1:focus ~ .focus-border {
  width: 100%;
  transition: 0.4s; }

.contact-form-textarea {
  height: 100%;
  width: 100%;
  border: none;
  padding-left: 10px;
  font-family: 'Roboto Slab', serif;
  resize: none; }

.contact-submit-button {
  margin-top: 50px;
  font-family: 'Josefin Slab', serif;
  background-color: Transparent;
  background-repeat: no-repeat;
  width: 300px;
  height: 100px;
  border: 2px solid white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 30px;
  color: white;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  transform: scale(1);
  transition: transform .25s ease; }

.contact-submit-button:hover {
  transform: scale(1.05);
  transition: transform .25s ease-out; }

.spinner-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 60px; }

.contact-spinner-txt {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  margin-top: 15px;
  font-size: 120%; }

.website-by {
  position: fixed;
  bottom: 0px;
  left: 0px;
  color: white;
  font-size: 10px; }
