.booking-button {
  margin-top: 40px;
  --offset: 10px;
  --border-size: 2px;
  font-family: 'Roboto Slab', serif;
  font-weight: bold;
  display: block;
  position: relative;
  padding: 1.5em 3em;
  appearance: none;
  border: 0;
  background: transparent;
  color: black;
  text-transform: uppercase;
  letter-spacing: .25em;
  outline: none;
  cursor: pointer;
  margin-bottom: 70px;
  font-weight: bold;
  border-radius: 0;
  box-shadow: inset 0 0 0 var(--border-size) currentcolor;
  transition: background .8s ease; }
  .booking-button:hover {
    background: rgba(255, 255, 255, 0.03); }
  .booking-button__horizontal, .booking-button__vertical {
    position: absolute;
    top: var(--horizontal-offset, 0);
    right: var(--vertical-offset, 0);
    bottom: var(--horizontal-offset, 0);
    left: var(--vertical-offset, 0);
    transition: transform .8s ease;
    will-change: transform; }
    .booking-button__horizontal::before, .booking-button__vertical::before {
      content: '';
      position: absolute;
      border: inherit; }
  .booking-button__horizontal {
    --vertical-offset: calc(var(--offset) * -1);
    border-top: var(--border-size) solid currentcolor;
    border-bottom: var(--border-size) solid currentcolor; }
    .booking-button__horizontal::before {
      top: calc(var(--vertical-offset) - var(--border-size));
      bottom: calc(var(--vertical-offset) - var(--border-size));
      left: calc(var(--vertical-offset) * -1);
      right: calc(var(--vertical-offset) * -1); }
  .booking-button:hover .booking-button__horizontal {
    transform: scaleX(0); }
  .booking-button__vertical {
    --horizontal-offset: calc(var(--offset) * -1);
    border-left: var(--border-size) solid currentcolor;
    border-right: var(--border-size) solid currentcolor; }
    .booking-button__vertical::before {
      top: calc(var(--horizontal-offset) * -1);
      bottom: calc(var(--horizontal-offset) * -1);
      left: calc(var(--horizontal-offset) - var(--border-size));
      right: calc(var(--horizontal-offset) - var(--border-size)); }
  .booking-button:hover .booking-button__vertical {
    transform: scaleY(0); }

button:focus {
  outline: 0; }
